body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}
.bg-fullscreen{
  width:100%;
  height:90vh;
  background: -webkit-linear-gradient(right bottom, yellow, purple);
}
.centered{
  margin-top:5%;
  text-align:center;
}
.text-center{
  text-align:center;
}
.main-padded{
  position:absolute;
  top:25%;
  left:0px;
  right:0px;
  z-index:10;
  padding:0 5%;
  color:white;
}
.mainTitle{
  color:rgb(214, 243, 248);
  font-weight:bold;
  font-size:4em;
  text-shadow:1px 1px 12px black;
}

.head-title-typed{
  color:white;
  font-size:1em;
}
.typed-cursor {
  font-size:1em;
  color:rgb(13, 158, 255);
  font-weight:0;
}
.typed-fade-out {
  background-color:#797676;
  -webkit-transition:opacity 2s ease;
  -o-transition:opacity 2s ease;
  transition:opacity 2s ease;
}

/* About section */
#about{
  position:relative;
  padding: 5% 0;
  width:100%;
  margin:0 auto;
  background: -webkit-linear-gradient(right bottom, yellow, purple);
  /* background: url('img/computer.jpg');
  background-size:cover;
  background-attachment:fixed; */
  /* background: rgb(0, 183, 255) ;
  background: -webkit-linear-gradient(left top,rgb(2, 95, 218),rgb(0, 183, 255) );
  background: -o-linear-gradient(bottom right,rgb(2, 95, 218),rgb(0, 183, 255));
  background: -moz-linear-gradient(bottom right,rgb(2, 95, 218),rgb(0, 183, 255)); 
  background: linear-gradient(to bottom right,rgb(2, 95, 218),rgb(0, 183, 255)); */
}
#about i{
  margin-top:5%;
  color:white;
  text-align:center;
  display:block;
  text-shadow:2px 2px 18px white;
}
#about .row{
  margin:0 auto;
}
#about p{
  color:white;
}
.h1-white{
  text-align:center;
  color: rgb(241, 240, 240);
  text-shadow:2px 2px 8px black;
}
.h1-black{
  text-align:center;
  color: rgb(41, 40, 40);
}
.about-h1{
  margin-top:1%;
  font-size:1.5em;
  color:white;
  text-shadow:2px 2px 8px black;
  text-align:center;
}
.project-h1{
  margin-top:1%;
  font-size:1.5em;
  color:black;
  text-align:center;
}
.btn-custom{
  color:white;
  text-transform:none;
  font-weight:bold;
  font-size:0.8em;
  background: rgb(0, 183, 255) ; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left top,rgb(2, 95, 218),rgb(0, 183, 255) ); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(bottom right,rgb(2, 95, 218),rgb(0, 183, 255)); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear,left top, right bottom,from(rgb(2, 95, 218)),to(rgb(0, 183, 255)));
  background: -webkit-linear-gradient(top left,rgb(2, 95, 218),rgb(0, 183, 255));
  background: -o-linear-gradient(top left,rgb(2, 95, 218),rgb(0, 183, 255));
  background: linear-gradient(to bottom right,rgb(2, 95, 218),rgb(0, 183, 255));
  border:0.01px solid grey ;
  border-radius:20px;
  -webkit-box-shadow:0px 1px 5px rgb(66, 66, 66);
          box-shadow:0px 1px 5px rgb(66, 66, 66);
  -webkit-transition: color 1s ease;
  -o-transition: color 1s ease;
  transition: color 1s ease;
}
.btn-custom2{
  color:white;
  text-transform:none;
  font-weight:bold;
  font-size:0.8em;
  background:-webkit-linear-gradient(right bottom, rgb(163, 235, 248),rgb(0, 87, 128));
  border:0.01px solid grey ;
  border-radius:20px;
  -webkit-box-shadow:0px 1px 5px rgb(66, 66, 66);
          box-shadow:0px 1px 5px rgb(66, 66, 66);
  -webkit-transition: color 1s ease;
  -o-transition: color 1s ease;
  transition: color 1s ease;
}
.btn-custom:hover{
  color: black;
  font-weight:bold;
  background:-webkit-linear-gradient(right bottom, rgb(163, 248, 163),green);
}
.btn-custom2:hover{
  color: black;
  font-weight:bold;
  background:-webkit-linear-gradient(right bottom, rgb(163, 248, 163),green);
}
nav{
 /* background: url('https://www.basbleu.org/wp-content/uploads/white-brick-background.jpg'); */
 background: rgb(0, 183, 255) ;
  background: -webkit-linear-gradient(left top,rgb(2, 95, 218),rgb(0, 183, 255) );
  background: -o-linear-gradient(bottom right,rgb(2, 95, 218),rgb(0, 183, 255)); 
  background: -webkit-gradient(linear,left top, right bottom,from(rgb(2, 95, 218)),to(rgb(0, 183, 255))); 
