nav ul li a{
  font-weight:bold;
  color:rgb(255, 255, 255);
}
/* projects */
#projects{
  background: -webkit-linear-gradient( rgb(250, 229, 210),rgb(195, 247, 240));
}
#projects img{
  width:100%;
  padding: 0 5%;
}
.skill{
  text-align:center;
  margin: 0 2%;
  border:0.05px solid transparent;
  border-radius: 15px;
  -webkit-box-shadow:1px 1px 5px grey;
          box-shadow:1px 1px 5px grey;
  padding: 1% 2%;
  display:inline;
  background-color: rgb(231, 103, 178);
  color:white; 
  opacity: 0.7;
}
.page-link{
  font-size:1.5em;
}
.page-link:hover{
  cursor:pointer;
}
#work-with-me{
  display:block;
  padding-bottom:5%;
  text-decoration:underline;
}
#work-with-me:hover{
  cursor:pointer;
}
.hover-pointer:hover{
  cursor:pointer;
  text-decoration:underline;
}
.projects p{
  padding: 0 10%;
  color:grey;
  text-align:center;
}
/* contact section */
#contact{
  padding:5% 15%;
  width:100%;
  background: -webkit-linear-gradient(right bottom, rgb(47, 0, 255), purple);
}




/* footer */
#footer .row{
  padding: 0 35%;
}
#footer i{
  color:rgba(255, 255, 255, 0.699);
  -webkit-transition: opacity 0.5s ease;
  -o-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}
#footer i:hover{
  opacity: 0.3;
}

@media (max-width:768px){
  #footer .row{
    padding: 0 25%;
  }
  .projects img{
    margin-top: 5%;
  }
}


